import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';





/* ################################################### */
/*          UI Framework         */
/* ################################################### */

import { RaisedButton, FlatButton }                             from 'material-ui';
import FlatPagination                               from 'material-ui-flat-pagination';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import TextField                                    from 'material-ui/TextField';
import Paper                                        from 'material-ui/Paper';

import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionMaintenanceUser , { ReduxEnumMaintenanceUser } from '../../../_redux/action/reduxAction.maintenance.user';






/* ################################################### */
/*          page         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';







/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';









/* ################################################### */
/* 		 util		 */
/* ################################################### */

import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';
import { DateUtil }		                            from '../../../_util/util.date';





import '../s.maintenance.css';
import './s.user.css';









export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxMaintenanceUser' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {


                        if ( ReduxEnumMaintenanceUser.INIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUser.LIST_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestListDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUser.PRINT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestPrintDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUser.DOWNLOAD_CSV_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDownloadCsvDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUser.DOWNLOAD_PDF_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestDownloadPdfDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumMaintenanceUser.RESET_SESSION_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestResetSessionDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }


                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (


         class ComponentMaintenanceUser extends ComponentAbstract {

            constructor( props ) {
                super( props );

                console.log( 'ComponentMaintenanceUser : constructor : *** : [' + this.uuid() + '] ' , props );



                this.state = {
                    isLoading : false

                    , data : {
                        currentPage : 0
                        , totalPage : 1000

                        , branch : []
                        , status : []
                        , role : []
                        , assignmentGroup : []
                    }

                    , form : {
                        userId : ''
                        , userName : ''
                        , branch : ''
                        , status : ''
                        , role : ''
                    }

                    , result : [ ]

                }

            }







            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestInit();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        event
                ################################################################################################################################################
                ################################################################################################################################################
            */
            onClickViewDetail( userId ) {
                console.log( 'ComponentMaintenanceUser : onClickViewDetail : *** : userId => ' , userId );

                let recordObj = {};
                for ( let i = 0 ; i < this.state.result.length; i ++ ) {
                    let tmpRecord = this.state.result[ i ];

                    if ( tmpRecord[ 'bankuserId' ] == userId ) {
                        recordObj = tmpRecord;
                    }
                }

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.MAINTENANCE_USER_DETAIL )
                    , {
                        action : 'detail'
                        , info : recordObj
                        , data : {
                            status      : this.state.data.status
                            , role      : this.state.data.role
                            , branch    : this.state.data.branch
                            , assignmentGroup : this.state.data.assignmentGroup
                        }
                    }
                );
            }
            onClickAdd() {
                console.log( 'ComponentMaintenanceUser : onClickAdd : *** ' );

                HelperNavigate.route(
                    HelperNavigate.mapPath( HelperNavigateENUM.MAINTENANCE_USER_DETAIL )
                    , {
                        action : 'new'
                        , info : {}
                        , data : {
                            status      : this.state.data.status
                            , role      : this.state.data.role
                            , branch    : this.state.data.branch
                            , assignmentGroup : this.state.data.assignmentGroup
                        }
                    }
                );
            }
            onClickSearch() {
                console.log( 'ComponentMaintenanceUser : onClickSearch : *** ' );

                let data = this.state.data;
                data[ 'currentPage' ] = 0;
                this.setState(
                    { data : data }
                    , () => {
                        this.requestList();
                    }
                );
            }
            onClickPrint() {
                console.log( 'ComponentMaintenanceUser : onClickPrint : *** ' );

                this.requestPrint();

            }
            onClickResetSession() {
                console.log( 'ComponentMaintenanceUser : onClickResetSession : *** ' );

                this.requestResetSession();

            }
            onClickDownloadCsv() {
                console.log( 'ComponentMaintenanceUser : onClickDownloadCsv : *** ' );

                this.requestDownloadCsv();

            }
            onClickDownloadPdf() {
                console.log( 'ComponentMaintenanceUser : onClickDownloadPdf : *** ' );

                this.requestDownloadPdf();

            }











            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				request
        		################################################################################################################################################
        		################################################################################################################################################
        	*/

            requestInit() {
                console.log( 'ComponentMaintenanceUser : requestInit : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rInit(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestInitDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestInitDone : *** : result => ' , result );

                let data = this.state.data;
                data[ 'branch' ]                = result[ 'branch' ];
                data[ 'role' ]                  = result[ 'role' ];
                data[ 'status' ]                = result[ 'status' ];
                data[ 'assignmentGroup' ]       = result[ 'assignmentGroup' ];
                this.setState(
                    {
                        isLoading : false
                        , data : data
                    }
                );
                this.dispatch( ReduxActionMaintenanceUser.rRequestEnd( ) );
            }


            requestList() {
                console.log( 'ComponentMaintenanceUser : requestList : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rList(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            paging      : ( ( this.state.data.currentPage / 10 ) + 1 )
                                            , userId    : this.state.form.userId
                                            , userName  : this.state.form.userName
                                            , branch    : this.state.form.branch
                                            , status    : this.state.form.status
                                            , role      : this.state.form.role
                                        }
                                    )
                                )
                            }
                        );

                    }
                )

            }
            requestListDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestListDone : *** : result => ' , result );

                this.setState( {
                    isLoading : false
                    , result : result
                } );

                this.dispatch(
                    ReduxActionMaintenanceUser.rRequestEnd( )
                );
            }



            requestDownloadCsv() {
                console.log( 'ComponentMaintenanceUser : requestDownloadCsv : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rDownloadCSV(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestDownloadCsvDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestDownloadCsvDone : *** : result => ' , result );

                this.setState( { isLoading : false } );
                this.dispatch(
                    ReduxActionMaintenanceUser.rRequestEnd( )
                );
            }



            requestDownloadPdf() {
                console.log( 'ComponentMaintenanceUser : requestDownloadPdf : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rDownloadPDF(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestDownloadPdfDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestDownloadPdfDone : *** : result => ' , result );

                this.setState( { isLoading : false } );
                this.dispatch(
                    ReduxActionMaintenanceUser.rRequestEnd( )
                );
            }



            requestResetSession() {
                console.log( 'ComponentMaintenanceUser : requestResetSession : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rResetSession(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestResetSessionDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestResetSessionDone : *** : result => ' , result );


                this.setState( { isLoading : false } );
                this.dispatch(
                    ReduxActionMaintenanceUser.rRequestEnd( )
                );
            }



            requestPrint() {
                console.log( 'ComponentMaintenanceUser : requestPrint : *** ' );


                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionMaintenanceUser.rPrint(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestPrintDone( result ) {
                console.log( 'ComponentMaintenanceUser : requestPrintDone : *** : result => ' , result );


                this.setState( { isLoading : false } );
                this.dispatch(
                    ReduxActionMaintenanceUser.rRequestEnd( )
                );
            }













            /*
        		################################################################################################################################################
        		################################################################################################################################################
        				render
        		################################################################################################################################################
        		################################################################################################################################################
        	*/
            render() {
                return (

                    <div>
                        <Paper>
                            <h3 style={ { paddingLeft: 25, paddingTop: 12 } }>
                                    Bank User Management
                            </h3>

                            <hr/>

                             <div
                                className={ ' userMaintenance-user-form-marginBottom-10 ' }
                                style={ { fontWeight : 'bolder', marginLeft: 28 } }>
                                    Search by / View Bank User
                            </div>

                            <br/>

                            <div style={ { paddingLeft: 25, paddingBottom: 10  } }>

                                <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                    <div style={ { flex: 0.2 } }>

                                        <p style={ { paddingTop: 12 } }>User ID</p>

                                    </div>

                                    <div style={ { flex: 0.2 } }>

                                        <TextField
                                                        style={ { width: '130%' } }
                                                        underlineStyle={ { borderColor: '#616161' } }
                                                        underlineFocusStyle={ { borderColor: '#000' } }
                                                        hintText={ "User ID" }
                                                        value={ this.state.form.userId }

                                                        onChange={
                                                            ( event , newValue ) => {
                                                                let form = this.state.form;
                                                                form[ 'userId' ] = newValue;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                    />

                                    </div>

                                </div>

                                    <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                        <div style={ { flex: 0.2 } }>

                                            <p style={ { paddingTop: 12 } }>User Name</p>

                                        </div>

                                        <div style={ { flex: 0.2 } }>

                                            <TextField
                                                        style={ { width: '130%' } }
                                                        underlineStyle={ { borderColor: '#616161' } }
                                                        underlineFocusStyle={ { borderColor: '#000' } }
                                                        hintText={ "User Name" }
                                                        value={ this.state.form.userName }

                                                        onChange={
                                                            ( event , newValue ) => {
                                                                let form = this.state.form;
                                                                form[ 'userName' ] = newValue;
                                                                this.setState( { form : form } );
                                                            }
                                                        }

                                                    />

                                        </div>

                                    </div>

                                        <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                            <div style={ { flex: 0.2 } }>

                                                <p style={ { paddingTop: 32 } }>Role</p>

                                            </div>

                                            <SelectField
                                                                style={ { flex: 0.26 } }
                                                                floatingLabelText={ "Role" }
                                                                value={ this.state.form.role }
                                                                onChange={
                                                                    ( event, index, value ) => {
                                                                        let form = this.state.form;
                                                                        form[ 'role' ] = value;
                                                                        this.setState( { form : form } );
                                                                    }
                                                                } >
                                                    {
                                                        this.state.data.role.map(
                                                            ( value , index ) => {
                                                                return (
                                                                    <MenuItem
                                                                                key={ '_role_' + index }
                                                                                value={ value[ 'code' ] }
                                                                                primaryText={ value[ 'label' ] } />
                                                                );
                                                            }
                                                        )

                                                    }

                                            </SelectField>

                                            

                                        </div>

                                         <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                            <div style={ { flex: 0.2 } }>

                                                <p style={ { paddingTop: 32 } }>Branch</p>

                                            </div>

                                            <SelectField
                                                            style={ { flex: 0.26 } }
                                                            floatingLabelText={ "Branch" }
                                                            value={ this.state.form.branch }
                                                            onChange={
                                                                ( event, index, value ) => {
                                                                    let form = this.state.form;
                                                                    form[ 'branch' ] = value;
                                                                    this.setState( { form : form } );
                                                                }
                                                            } >

                                                    {
                                                        this.state.data.branch.map(
                                                            ( value , index ) => {
                                                                return (
                                                                    <MenuItem
                                                                                key={ '_branch_' + index }
                                                                                value={ value[ 'code' ] }
                                                                                primaryText={ value[ 'label' ] } />
                                                                );
                                                            }
                                                        )

                                                    }

                                            </SelectField>

                                            

                                        </div>

                                           <div style={ { display: 'flex' , justifyContent: 'flex-start' } } >

                                                <div style={ { flex: 0.2 } }>

                                                    <p style={ { paddingTop: 32 } }>Status</p>

                                                </div>

                                                <SelectField
                                                                style={ { flex: 0.26 } }
                                                                floatingLabelText={ "Status" }
                                                                value={ this.state.form.status }
                                                                onChange={
                                                                    ( event, index, value ) => {
                                                                        let form = this.state.form;
                                                                        form[ 'status' ] = value;
                                                                        this.setState( { form : form } );
                                                                    }
                                                                } >

                                                        {
                                                            this.state.data.status.map(
                                                                ( value , index ) => {
                                                                    return (
                                                                        <MenuItem
                                                                                    key={ '_status_' + index }
                                                                                    value={ value[ 'code' ] }
                                                                                    primaryText={ value[ 'label' ] } />
                                                                    );
                                                                }
                                                            )

                                                        }

                                                </SelectField>
                                
                                            </div>

                                            <div style={ {display: 'flex', justifyContent: 'flex-end', width: '46%', marginTop: 20 } }>
                                                
                                                <div style={ { flex: 5.5  } } />

                                                    <RaisedButton
                                                        style={ { flex: 1  } }
                                                        label={ "Search" }
                                                        secondary={ true }
                                                        onClick={ () => {
                                                            this.onClickSearch();
                                                        } }
                                                    />
                                              

                                            </div>

                                    </div>

                                        <div style={ { display: 'flex', justifyContent: 'center' } } >

                                            <FlatPagination
                                                    offset={ this.state.data.currentPage }
                                                    limit={ 10 }
                                                    total={ this.state.data.totalPage }
                                                    onClick={
                                                        ( e , offset ) => {

                                                            let data = this.state.data;
                                                            data[ 'currentPage' ] = offset;
                                                            this.setState(
                                                                { data : data }
                                                                , () => {
                                                                    this.requestList();
                                                                }
                                                            );

                                                        }
                                                    }

                                                    nextPageLabel={ 'Next' }
                                                    previousPageLabel={ 'Previous' }

                                                    otherPageLabelStyle={ {
                                                        fontSize : '16px'
                                                    } }
                                                    currentPageLabelStyle={ {
                                                        fontSize : '16px'
                                                    } }

                                            />

                                        </div>

                                        <Table fixedHeader={ false }
                                                style={ { width: "100%" } } tableLayout="auto">

                                            <TableHeader
                                                    adjustForCheckbox={ false }
                                                    displaySelectAll={ false }>
                                                <TableRow>
                                                    <TableHeaderColumn> User ID </TableHeaderColumn>
                                                    <TableHeaderColumn> User Name </TableHeaderColumn>
                                                    <TableHeaderColumn> Branch ID </TableHeaderColumn>
                                                    <TableHeaderColumn> Branch Name </TableHeaderColumn>
                                                    <TableHeaderColumn> Department </TableHeaderColumn>
                                                    <TableHeaderColumn> Role </TableHeaderColumn>
                                                    <TableHeaderColumn> Status </TableHeaderColumn>
                                                    <TableHeaderColumn> Last Updated </TableHeaderColumn>
                                                    <TableHeaderColumn> Login Status </TableHeaderColumn>
                                                </TableRow>
                                            </TableHeader>

                                            <TableBody displayRowCheckbox={ false } showRowHover={ true }>

                                                {
                                                    this.state.result.map(
                                                        ( value , index ) => {
                                                            return (

                                                                <TableRow
                                                                        key={ '_userProfile_' + index } >

                                                                    <TableRowColumn>
                                                                        { value[ 'bankuserId' ] }
                                                                    </TableRowColumn>
                                                                    <TableRowColumn>
                                                                        <a
                                                                                href={ 'javascript:void( 0 )' }
                                                                                onClick={ () => {
                                                                                    this.onClickViewDetail( value[ 'bankuserId' ] );
                                                                                } } >
                                                                            { value[ 'username' ] }
                                                                        </a>
                                                                    </TableRowColumn>
                                                                    <TableRowColumn> { value[ 'branchCode' ] } </TableRowColumn>
                                                                    <TableRowColumn> { value[ 'branchCode' ] } </TableRowColumn>
                                                                    <TableRowColumn> - </TableRowColumn>
                                                                    <TableRowColumn>
                                                                        {
                                                                            ( () => {
                                                                                let label = '-';
                                                                                for ( let i = 0 ; i < this.state.data.role.length; i ++ ) {
                                                                                    let tmpRoleObj = this.state.data.role[ i ];

                                                                                    if ( tmpRoleObj[ 'code' ] == value[ 'persona' ] ) {
                                                                                        label = tmpRoleObj[ 'label' ];
                                                                                    }
                                                                                }
                                                                                return label;
                                                                            } )()
                                                                        }
                                                                    </TableRowColumn>
                                                                    <TableRowColumn>
                                                                        {
                                                                            ( () => {
                                                                                let label = '-';
                                                                                for ( let i = 0 ; i < this.state.data.status.length; i ++ ) {
                                                                                    let tmpRoleObj = this.state.data.status[ i ];

                                                                                    if ( tmpRoleObj[ 'code' ] == value[ 'status' ] ) {
                                                                                        label = tmpRoleObj[ 'label' ];
                                                                                    }
                                                                                }
                                                                                return label;
                                                                            } )()
                                                                        }
                                                                    </TableRowColumn>
                                                                    <TableRowColumn>
                                                                        { DateUtil.formatDateTime( DateUtil.stringToDate ( value[ 'lastUpdatedDate' ] ) ) }
                                                                    </TableRowColumn>
                                                                    <TableRowColumn>
                                                                        { DateUtil.formatDateTime( DateUtil.stringToDate ( value[ 'lastUpdatedDate' ] ) ) }
                                                                    </TableRowColumn>

                                                                </TableRow>

                                                            );
                                                        }
                                                    )
                                                }

                                            </TableBody>

                                        </Table>

                                    <div className={ ' userMaintenance-textAlign-center ' } >

                                        <RaisedButton
                                                            className={ 'userMaintenance-margin-10' }
                                                            label={ "Print" }
                                                            onClick={ () => {
                                                                this.onClickPrint();
                                                            } } />

                                        <RaisedButton
                                                            className={ 'userMaintenance-margin-10' }
                                                            label={ "Reset Session" }
                                                            onClick={ () => {
                                                                this.onClickResetSession();
                                                            } } />

                                        <RaisedButton
                                                            className={ 'userMaintenance-margin-10' }
                                                            label={ "Download CSV" }
                                                            onClick={ () => {
                                                                this.onClickDownloadCsv();
                                                            } } />

                                        <RaisedButton
                                                            className={ 'userMaintenance-margin-10' }
                                                            label={ "Download PDF" }
                                                            onClick={ () => {
                                                                this.onClickDownloadPdf();
                                                            } } />


                                        <RaisedButton
                                                            className={ 'userMaintenance-margin-10' }
                                                            label={ "Add" }
                                                            onClick={ () => {
                                                                this.onClickAdd();
                                                            } } />


                                    </div>

                            </Paper>

                               <ComponentCustomLoading
                                visible={ this.state.isLoading }
                                />

                        </div>
                )
            }

        }

    )



